import {
  Button,
  FormControlLabel,
  Grid,
  Radio,
  RadioGroup,
  TextField,
} from "@mui/material";
import copy from "copy-to-clipboard";
import { useCallback } from "react";
import { Controller, useForm } from "react-hook-form";
interface FormValues {
  type?: string;
  params?: string;
  result?: string;
}

function App() {
  const { control, register, getValues, setValue } = useForm<FormValues>({
    defaultValues: { type: "1" },
  });

  const handleOnChangeParams = useCallback(() => {
    const values = getValues("params");
    const params = values
      ?.replace(/[-'"]/g, "")
      ?.replace(/[\s]/g, ",")
      ?.split(",");
    if (params && params?.length <= 0) {
      return;
    }
    const type = getValues("type") === "1" ? "String" : "Boolean";
    let results: string[] = [];
    params?.map((name) => {
      if (name && name !== "") {
        const param =
          '{"DataType":"' +
          type +
          '","Name":"' +
          name +
          '","Nullable":true,"Prompt":"' +
          name +
          '","ValidValues":{"OrderBy":{"Condition":"None"}}}';
        results?.push(param);
      }
    });
    if (results?.length <= 0) {
      return;
    }
    setValue("result", results.join(","));
    return;
  }, [getValues, setValue]);

  const handleOnClearText = useCallback(() => {
    setValue("params", "");
    setValue("result", "");
  }, [setValue]);

  const handleCopy = useCallback(() => {
    const params = getValues("result");
    if (params) {
      copy(params);
    }
  }, [getValues]);
  const handleChangeInput = useCallback(() => {
    setValue("result", "");
  }, [setValue]);

  return (
    <>
      <Grid
        container
        spacing={2}
        justifyContent="center"
        alignItems="center"
        marginTop={5}
        marginLeft={2}>
        <img
          src="data:image/png;base64,"
          alt=""
        />
        <Grid item xs={12} sm={11}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={12} textAlign="center">
              <h1>Active Report</h1>
            </Grid>
            <Grid item container>
              <Grid item container justifyContent="left" alignContent="left">
                <Grid item xs={1.5} md={1.5} xl={1.5} lg={1.5}>
                  <Controller
                    name="type"
                    control={control}
                    render={({ field }) => (
                      <RadioGroup {...field} row>
                        <FormControlLabel
                          value="1"
                          control={<Radio />}
                          label="String"
                          onChange={handleOnChangeParams}
                        />
                        <FormControlLabel
                          value="0"
                          control={<Radio />}
                          label="Boolean"
                          onChange={handleOnChangeParams}
                        />
                      </RadioGroup>
                    )}
                  />
                </Grid>
                <Grid
                  item
                  container
                  spacing={2}
                  xs={4}
                  md={4}
                  xl={4}
                  lg={4}
                  justifyContent="flex-start"
                  alignContent="flex-start"
                  alignItems="flex-start">
                  <Grid item>
                    <Button
                      variant="contained"
                      onClick={handleOnChangeParams}
                      fullWidth>
                      Create
                    </Button>
                  </Grid>

                  <Grid item>
                    <Button
                      variant="contained"
                      onClick={handleOnClearText}
                      fullWidth>
                      Clear
                    </Button>
                  </Grid>
                </Grid>
                <Grid
                  item
                  container
                  spacing={2}
                  xs={3}
                  md={3}
                  xl={3}
                  lg={3}
                  marginLeft={8.7}
                  justifyContent="flex-start"
                  alignContent="flex-start"
                  alignItems="flex-start">
                  <Grid item>
                    <Button variant="contained" onClick={handleCopy} fullWidth>
                      Copy
                    </Button>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Grid
          item
          container
          spacing={3}
          alignContent="center"
          justifyContent="center">
          <Grid item container xs={5.5} sm={5.5} lg={5.5} md={5.5}>
            <Grid item container>
              <TextField
                label="Params input"
                multiline
                rows={15}
                fullWidth
                inputProps={{ "data-testid": "params" }}
                {...register("params")}
                aria-readonly
                onChange={handleChangeInput}
              />
            </Grid>
          </Grid>
          <Grid item container xs={5.5} sm={5.5} lg={5.5} md={5.5} left={3}>
            <Grid item container>
              <Grid item xs={11} sm={11} lg={11} md={11}>
                <TextField
                  focused
                  multiline
                  rows={15}
                  fullWidth
                  inputProps={{ "data-testid": "result" }}
                  {...register("result")}
                  aria-readonly
                  disabled
                />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </>
  );
}

export default App;
